<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{changePasswordText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-form v-model="validForm">
                <v-text-field
                        v-model="oldPassword"
                        :rules="[rules.required]"
                        :append-icon="showOldPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showOldPassword ? 'text' : 'password'"
                        :label="currentPasswordText"
                        @click:append="showOldPassword = !showOldPassword"
                        required
                ></v-text-field>
                <v-text-field
                        ref="password"
                        v-model="password"
                        :rules="[rules.required, rules.min8Char, rules.samePassword]"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPassword ? 'text' : 'password'"
                        :label="newPasswordText"
                        :hint="min8CharText"
                        counter
                        @click:append="showPassword = !showPassword"
                        required
                ></v-text-field>
                <v-text-field
                        ref="password2"
                        v-model="password2"
                        :rules="[rules.required, rules.min8Char, rules.samePassword]"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPassword ? 'text' : 'password'"
                        :label="confirmPasswordText"
                        :hint="min8CharText"
                        counter
                        @click:append="showPassword = !showPassword"
                        required
                ></v-text-field>
            </v-form>

            <v-btn block color="primary" style="margin-top: 20px" @click="changePassword" :disabled="!validForm">
                {{updatePasswordText}}
            </v-btn>
        </v-card>
        <!--<small>
            <p align="center" style="margin-top: 10px; margin-bottom: 10px">
                <router-link to="/send-change-password-email">{{haveYouForgottenYourPasswordText}}</router-link>
            </p>
        </small>-->

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <p style="margin-bottom: 7px">{{yourPasswordHasBeenUpdatedText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="$router.go(-1)">{{continueText}}</v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: "SettingsAccountEditPassword",
        data() {
            return {
                validForm: false,
                snackbar: false,
                dialog: false,
                oldPassword: '',
                password: '',
                password2: '',
                snackbarText: null,
                showPassword: false,
                correctPasswordCombination: false,
                showOldPassword: false,
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    min8Char: value => (value.length >= 8 || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD'),
                    samePassword: () => this.correctPasswordCombination == true || this.$i18n.t('messages.ERROR_PASSWORDS_DONT_MATCH')
},
                email: this.$store.getters['user/getEmail'],
                closeText: null,
                updatePasswordText: null,
                min8CharText: null,
                changePasswordText: null,
                currentPasswordText: null,
                newPasswordText: null,
                haveYouForgottenYourPasswordText: null,
                confirmPasswordText: null,
                yourPasswordHasBeenUpdatedText: null,
                continueText: null
}
        },
        methods: {
            ...mapActions('user', ['login', 'updateAccountPassword']),
            changePassword: function () {
                let self = this;
                let resultLogin = self.login({
                    email: self.email,
                    password: self.oldPassword
                });

                resultLogin.then(() => {
                    let resultUpdateAccountPassword = self.updateAccountPassword(self.password);

                    resultUpdateAccountPassword.then(() => {
                        self.dialog = true;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                })
                    .catch(function (error) {
                        if (error == this.$i18n.t('messages.ERROR_WRONG_EMAIL_PASSWORD_COMBINATION')) {
                            self.snackbarText = this.$i18n.t('messages.ERROR_TEXT_WRONG_PASSWORD');
                        } else {
                            self.snackbarText = error;
                        }

                        self.snackbar = true;
                    });
            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.updatePasswordText = this.$i18n.t('messages.TEXT_UPDATE_PASSWORD');
            this.min8CharText = this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD');
            this.changePasswordText = this.$i18n.t('messages.TEXT_CHANGE_PASSWORD');
            this.currentPasswordText = this.$i18n.t('messages.TEXT_CURRENT_PASSWORD');
            this.newPasswordText = this.$i18n.t('messages.TEXT_NEW_PASSWORD');
            this.haveYouForgottenYourPasswordText = this.$i18n.t('messages.TEXT_HAVE_YOU_FORGOTTEN_YOUR_PASSWORD');
            this.confirmPasswordText = this.$i18n.t('messages.TEXT_CONFIRM_PASSWORD');
            this.yourPasswordHasBeenUpdatedText = this.$i18n.t('messages.TEXT_YOUR_PASSWORD_HAS_BEEN_UPDATED');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
        },
        watch: {
            password: function (value) {
                if (value === this.password2) {
                    this.correctPasswordCombination = true;
                    this.$refs.password2.validate();
                } else {
                    this.correctPasswordCombination = false;
                }
            },

            password2: function (value) {
                if (value === this.password) {
                    this.correctPasswordCombination = true;
                    this.$refs.password.validate();
                } else {
                    this.correctPasswordCombination = false;
                }
            }
}
    }
</script>

<style scoped>

</style>